<template>
	 <div id="category">
        <search-topbar v-bind:title="title"></search-topbar>
        <swipe-topbar v-bind:topTitles="topTitles"></swipe-topbar>
        <category-list v-bind:categoryData="categoryData"></category-list>
    </div>
</template>
<script>
    import CategoryList from './CategoryList'
    import SearchTopbar from './SearchTopbar'
    import SwipeTopbar from './SwipeTopbar'
    export default {
        name: 'category',
        data () {
            return {
                url: '/static/json/category.json',
                categoryData: [],
                title: '商品分类'
            } 
        },
        computed: {
            topTitles () {
                return this.categoryData.map((item, index) => {
                    return item.title.category_name
                })
            }
        },
        components: {
            'category-list': CategoryList,
            'search-topbar': SearchTopbar,
            'swipe-topbar': SwipeTopbar
    	},
        methods: {
            getCategoryData () {
                this.$http.get(this.url).then(function(response) {
                    this.categoryData = response.body;
                })
            }
        },
        created () {
            this.getCategoryData();
        }
    }
</script>



