<template>
	<header class="top-banner">
        <a href="javascript:history.back()" class="back-btn">
            <i class="iconfont icon-back"></i>
        </a>
        <h2>
            {{ title }}
        </h2>
        <router-link :to="'/search'">
            <i class="iconfont icon-search"></i>
        </router-link>
    </header>
</template>
<script>
	export default {
		name: 'search-topbar',
		props: ['title']
	}
</script>
<style>
	
	header.top-banner {
	    display: flex;
	    font-size: 1.6rem;
	    border-bottom: 1px solid #d9d9d9;
	    background: #f5f5f5;
	}
	header.top-banner h2 {
	    flex: 1;
	    text-align: center;
	    line-height: 45px;
	}

	header.top-banner > a {
	    padding: 6px 10px;
	    color: #999;
	    font-size: 1.4rem;
	}
	header.top-banner > a .iconfont {
	    font-size: 2.2rem;
	}
</style>


