<template>
  <div>
  	<loading></loading>
    <router-view></router-view>
    <footer-bar v-if="paths.indexOf($route.name)!==-1"></footer-bar>
  </div>
</template>

<script>
import Loading from './components/Loading'
import FooterBar from './components/FooterBar'
export default {
  name: 'app',
  data () {
  	return {
  		paths: ['index','category','cart','user']
  	}
  },
  components: {
  	Loading,
    FooterBar
  }
}
</script>




