<template>
	<div>
		<search-topbar v-bind:title="title"></search-topbar>
		<search-list></search-list>
	</div>
</template>
<script>
	import SearchTopbar from './SearchTopbar'
	import SearchList from './SearchList'
	export default {
		name: 'list',
		data () {
			return {
				title: '商品列表'
			}
		},
		components: {
			'search-topbar': SearchTopbar,
			'search-list': SearchList
		}
	}
</script>




