<template>
	<div id="productList">
	    <ul>
	        <li v-for="product in productList">
	            <router-link :to="'/product?id='+product.product_id" class="clearfix">
	                <div class="img-aside">
	                    <img :src="product.image_url" alt="">
	                </div>
	                <div class="tips">
	                    <h2 class="ellipsis">{{ product.product_name }}</h2>
	                    <p class="sub-title">{{ product.product_desc }}</p>
	                    <p class="price">&yen; {{ product.price_min }}</p> 
	                </div> 
	            </router-link>
	        </li>
	    </ul>
	</div>
</template>
<script>
	export default {
		name: 'search-list',
		data () {
			return {
				url: '/static/json/searchList.json',
				productList: []
			}
		},
		methods: {
			getSearchData () {
				this.$http.get(this.url).then(response => {
					console.log(response)
					this.productList = response.body;
				})
			}
		},
		mounted () {
			this.getSearchData();
		}
	}
</script>
<style scoped>
	#productList {
	    background-color: #f5f5f5;
	}
	#productList ul a {
	    display: flex;
	    position: relative;
	    align-items: center;
	    background-color: #fff;
	}

	#productList ul .tips {
	   width: 50%;
	   padding: 0 5%;
	}
	#productList ul .tips h2 {
	    color:#333;
	    font-size: 1.6rem;
	}
	#productList ul .tips .sub-title {
	    color:#666;
	    font-size: 1.4rem;
	    margin: 2% 0;
	    text-overflow: ellipsis;
	    overflow: hidden;
	    display: -webkit-box;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical;
	    word-break: break-all;
	}
	#productList ul .tips .price {
	    color:#ff6000;
	    font-size: 1.8rem;
	}
	#productList ul li {
		margin-bottom: 2px;
	}
	.img-aside {
		flex-basis: 32%;
	}
</style>


