<template>
	<div class="top-news">
	    <div class="news-logo">
	        <img src="../assets/img/news_logo.png" alt="">
	    </div>
	    <a v-bind:href="item.path"_blank class="news-title ellipsis" v-for="(item, index) in headProduct" v-show="index === activeIndex">
	        <span>|</span>
	        {{ item.title }}
	    </a>
	</div>
</template>

<script>
	export default {
		name: 'top-news',
		data () {
			return {
				activeIndex: 0,
				headProduct: [{
					title: '小米电视品牌日明天见，最高立减1000元',
					path: 'https://cdn.cnbj0.fds.api.mi-img.com/b2c-data-mishop/bc0e998e59f6.html?2&needValidHost=false&zb_news_id=780'
				},{
					title: '小米周报5.15-5.21一周新鲜事',
					path: 'https://cdn.cnbj0.fds.api.mi-img.com/b2c-data-mishop/22f8d7bc8ca7.html?2&needValidHost=false&zb_news_id=779'
				},{
					title: '爱要怎么说出口？520适合表白的手机推荐',
					path: 'https://cdn.cnbj0.fds.api.mi-img.com/b2c-data-mishop/c68e795bb06b.html?2&needValidHost=false&zb_news_id=778'
				}]
			}
		},
		methods: {
			tab () {
				this.timer = setInterval(() => {
					this.activeIndex++;
					this.activeIndex %= this.headProduct.length
				}, 5000)
			}
		},
		mounted () {
			this.tab()
		}
	}
</script>
<style scoped>
	.top-news {
	    display: flex;
	}
	.news-logo {
	    max-width: 78px;
	}
	.news-title {
	    flex: 1;
	    line-height: 39px;
	    font-size: 1.4rem;
	    color:#333;
	}
	.news-title span {
	    margin-right: 4px;
	}
</style>



