<template>
	<div>
		<search-bar v-on:getSearchData="getSearchData"></search-bar>
		<hot-search v-if="!resultList.length"></hot-search>
		<search-result v-else v-bind:resultList="resultList"></search-result>
	</div>
</template>
<script>
	import SearchBar from './SearchBar'
	import HotSearch from './HotSearch'
	import SearchResult from './SearchResult'
	export default {
		name: 'search-index',
		components: {
			'search-bar': SearchBar,
			'hot-search': HotSearch,
			'search-result': SearchResult
		},
		data () {
			return {
				resultList: [],
				url: 'http://search.mi.com/search/expand'
			}
		},
		methods: {
			getSearchData (keywords) {
				this.$http.jsonp(this.url,{
					params: {
						keyword: keywords
					},
					jsonp:"jsonpcallback"
				}).then(response => {
					this.resultList = response.body
				})
			}
		}
	}
</script>


