<template>
	<div id="product">
	    <section>
	        <div class="head-product">
	            <a>
	                <img src="../assets/img/p1.jpg" alt="">
	            </a>
	        </div>
	        <ul class="product-list" v-for="items in productlist">
	            <li v-for="item in items.body.items">
	                <router-link :to="{path: 'product', query: {id: item.action.path}}">
	                    <div>
	                        <img :src="item.img_url" alt="">
	                    </div>
	                    <div class="tips">
	                        <h2 class="ellipsis">{{item.product_name}}</h2>
	                        <p class="sub-title ellipsis">{{item.product_brief}}</p>
	                        <p class="price">&yen; {{item.product_price}}</p> 
	                    </div> 
	                </router-link>
	            </li>
	        </ul>
	    </section>
	</div>
</template>
<script>
	export default {
		name: 'product-list',
		props: ['productlist'],
	}
</script>
<style scoped>
	#product .head-product {
	    overflow: hidden;
	    max-height:320px;
	}
	#product .product-list {
	    margin-top: 1%;
	    display: flex;
	    flex-wrap: wrap;
	    justify-content: space-between;
	}
	#product .product-list li {
	    /*flex: 1;*/
	    flex-basis: 49.5%;
	}
	#product .product-list li a {
	    display: block;
	}
	#product .product-list .tips {
	    padding:  8px 8%;
	}
	#product .product-list .tips h2 {
	    color:#333;
	    font-size: 1.4rem;
	}
	#product .product-list .tips .sub-title {
	    color:#666;
	    font-size: 1.2rem;
	}
	#product .product-list .tips .price {
	    color:#ff6000;
	    font-size: 1.6rem;
	}
</style>


