<template>
	<header id="searchBar">
	    <nav>
	        <router-link to="/">
	            <i class="iconfont icon-shouye"></i>
	        </router-link>
	        <input ref="search" v-on:input="getSearchData">
	        <a href="javascript:void(0);">
	            <i class="iconfont icon-search"></i>
	        </a>
	    </nav>
	</header>
</template>
<script>
	export default {
		name: 'search-bar',
		methods: {
			getSearchData (event) {
				this.$emit("getSearchData", this.$refs.search.value.trim())
			}
		}
	}
</script>
<style scoped>
	#searchBar {
	    height: 46px;
	}
	#searchBar > nav{
	    position: fixed;
	    z-index: 10;
	    width: 100%;
	    left: 0;
	    top: 0;
	    background: #f5f5f5;
	    display: flex;
	    font-size: 1.6rem;
	    padding: 6px 0;
	    border-bottom: 1px solid #d9d9d9;
	}
	#searchBar input {
	    font-size: 1.4rem;
	    outline: none;
	    border: 1px solid #d9d9d9;
	    border-radius: 4px;
	    padding: 3px 8px;
	    color: #333;
	    flex: 1;
	}
	#searchBar nav > a {
	    padding: 0 10px;
	    color: #999;
	    font-size: 1.4rem;
	}
	#searchBar nav > a .iconfont {
	    font-size: 2.2rem;
	}
</style>


