<template>
	<div id="searchResult">
	    <ul>
	        <li v-for="list in resultList">
	            <router-link to="/product?id=3486">
	            	{{ list.Key }}
	            </router-link>
	        </li>
	    </ul>
	</div>
</template>
<script>
	export default {
		name: 'search-result',
		props: ['resultList']
	}
</script>
<style>
	#searchResult {
	    background: #f5f5f5;
	}

	#searchResult li a {
	    display: block;
	    height: 40px;
	    line-height: 40px;
	    padding: 0 4%;
	    font-size: 1.4rem;
	}
</style>


