<template>
	<div id="recomment">
	    <h2>为你推荐</h2>
	    <ul class="recomment-list">
	        <li v-for="(item, index) in recommentList">
	            <router-link :to="{path: 'product', query: {id: item.action.path}}">
	                <img v-bind:src="item.image_url" alt="">
	                <div class="info">
	                    <p>{{ item.name }}</p> 
	                    <strong class="price">
	                        &yen;{{ item.price }}
	                    </strong>
	                </div>
	            </router-link>
	        </li>
	    </ul>
	</div>
</template>
<script>
	export default {
		name: 'recomment-items',
		data () {
			return {
				url: '/static/json/cart.json',
				recommentList: []
			}
		},
		methods: {
			getRecommentData () {
				this.$http.get(this.url).then(response => {
					console.log(response)
					this.recommentList = response.body.data.recom_list;
				})
			}
		},
		created () {
			this.getRecommentData();
		}
	}
</script>
<style scoped>
	#recomment h2 {
	    height: 40px;
	    line-height: 40px;
	    margin: 0 5%;
	    font-size: 1.4rem;
	}
	#recomment .recomment-list {
	    display: flex;
	    flex-wrap: wrap;
	    justify-content: space-between;
	}
	#recomment .recomment-list li {
	    flex-basis: 49%;
	}
	#recomment .recomment-list a {
	    display: block;
	    font-size: 1.4rem;
	} 
	#recomment .recomment-list .info {
	    padding: 4% 8%;
	}
	#recomment .recomment-list .info p {
	    font-size: 1.2rem;
	    margin-bottom: 2%;
	}
	#recomment .recomment-list .info .price {
	    color:#ff6000;
	}
</style>


