<template>
	<div>
	  	<header-nav></header-nav>
	  	<swiper-banner v-bind:bannerimgs="indexData.header.body.items"></swiper-banner>
	    <top-news></top-news>
	    <product-list v-bind:productlist="indexData.sections"></product-list>
	    <go-top></go-top>
	</div>
</template>
<script>
import HeaderNav from './HeaderNav'
import SwiperBanner from './SwiperBanner'
import GoTop from './GoTop'
import ProductList from './ProductList'
import TopNews from './TopNews'
export default {
  name: 'index',
  components: {
    'header-nav': HeaderNav,
    'swiper-banner': SwiperBanner,
    'go-top': GoTop,
    'product-list': ProductList,
    'top-news': TopNews
  },
  data () {
  	return {
  		url: '/static/json/index.json',
  		indexData: {
  			header: {
  				body: {
  					items: []
  				}
  			},
  			sections: []
  		}
  	}
  },
  methods: {
  	getIndexData: function() {
  		this.$http.get(this.url).then(function(response) {
  			this.indexData = response.body.data;
  		})
  	}
  },
  created: function() {
  	this.getIndexData()
  }
}
</script>



