<template>
	<div id="hotSearch">
	    <ul>
	        <li v-for="item in results">
	            <router-link to="/search/list" v-bind:style="{backgroundColor: item.back_n, borderColor: item.border_n, color: item.font_n}">{{ item.word }}</router-link>
	        </li>
	    </ul>
	</div>
</template>
<script>
	export default {
		name: 'hot-search',
		data () {
			return {
				results: [{"back_n":"#f8f2ec","border_n":"#fac288","font_n":"#484848","word":"降噪耳机"},{"back_n":"#e8f6f6","border_n":"#8cdade","font_n":"#484848","word":"红米Note 4X"},{"back_n":"#f2f6e9","border_n":"#bbd090","font_n":"#484848","word":"小米Max2"},{"back_n":"#f9eaea","border_n":"#fca0a7","font_n":"#484848","word":"红米4X"}]
			}
		}
	}
</script>
<style scoped>
	#hotSearch ul {
	    display: flex;
	    flex-wrap: wrap;
	    width: 92%;
	    margin: 4%;
	}
	#hotSearch ul li {
	    padding: 4px 4px;
	}
	#hotSearch ul li a{
	    display: block;
	    height: 32px;
	    line-height: 32px;
	    padding: 0 4px;
	    background-color: rgb(232, 246, 246);
	    border: 1px solid rgb(140, 218, 222);
	}
</style>


