<template>
	<div id="infoList">
	    <section v-for="group in categoryData" ref="sectionlist">
	        <div class="channel-banner">
	            <a>
	                <img :src="group.pic.items[0].img_url" alt="">
	            </a>
	        </div>
	        <h2 class="category-title">
	        	{{ group.title.category_name }}
	        </h2>
	        <ul class="list-wrap">
	            <li v-for="item in group.groups.items">
	                <router-link :to="{path: 'product', query: {id: item.action.path}}">
	                    <img :src="item.img_url" alt="">
	                    <p>
	                    	{{ item.product_name }}
	                    </p> 
	                </router-link>
	            </li>
	        </ul>
	    </section>

	</div>
</template>
<script>
	export default {
		name: 'category-list',
		props: ['categoryData']
	}
</script>
<style scoped>
	#infoList {
	    background: #f5f5f5;
	}
	.channel-banner {
	    padding: 3% 0;
	    background: #f5f5f5;    
	}
	.channel-banner a {
	    display: block;
	}
	#infoList .list-wrap {
	    display: flex;
	    flex-wrap: wrap;
	    justify-content: space-between;
	    padding-bottom: 4%;
	}
	#infoList .list-wrap li {
	    flex-basis: 30%;
	}
	#infoList .list-wrap li a{
		display: block;
		box-sizing: border-box;
		padding: 5%;
	}
	.category-title {
	    text-align: center;
	    font-size: 1.4rem;
	    color: #333;
	    height: 56px;
	    line-height: 56px;
	}
	#infoList > section {
	    background-color: #fff;
	}
	#infoList .list-wrap li p {
	    text-align: center;
	    text-overflow: ellipsis;
	    overflow: hidden;
	    white-space: nowrap;
	    font-size: 1.2rem;
	    color: #666;
	}
</style>


