<template>
	<nav class="product-category" v-bind:class="{'fix-active': fixFlag}">
	    <div class="product-category-wrapper">
	        <div class="nav-info" ref="navinfo">
	        	<a  v-bind:class="{active: index === activeIndex}" 
	        	    v-for="(title, index) in topTitles"
	        		v-on:click="tab(index)"
	        		ref="link">
	        		{{ title }}
	        	</a>
	        </div>
	    </div>
	</nav>
</template>
<script>
	export default {
		name: 'swipe-topbar',
		props: ['topTitles'],
		data () {
			return {
				activeIndex : 0,
				offsetLeftList: [],
				documentPositions: [],
				fixFlag: false
			}
		},
		methods: {
			getPos () {
				// DOM 更新了
				var eles = this.$refs.link;
				var sum = 0;
				for(var i = 0, len = eles.length; i < len; i++) {
					this.offsetLeftList.push(sum)
					sum += eles[i].offsetWidth;
				}
				
			},
			getDocPos () {
				var st = document.body.scrollTop || document.documentElement.scrollTop;
				var eles = document.querySelectorAll("#infoList section");
				for(var i = 0, len = eles.length; i < len; i++) {
					this.documentPositions.push(st + eles[i].getBoundingClientRect().top - 64);
				}
			},
			scroll () {
				window.addEventListener("scroll", () => {
					var st = document.body.scrollTop || document.documentElement.scrollTop;
					if(st > 100) {
						this.fixFlag = true;
					}else {
						this.fixFlag = false;
					}
				}, false)
			},
			tab (index) {
				this.activeIndex = index;
				this.$refs.navinfo.scrollLeft = this.offsetLeftList[index];
				
				window.scrollTo(0, this.documentPositions[index])
			}
		},
		updated () {
			if(this.offsetLeftList.length == 0) {
				this.getPos();
				this.getDocPos();
				this.scroll();
			}

		}
	}
</script>
<style scoped>
	.fix-active {
		position: fixed;
		top: 0;
		background-color: #fff;
		width: 100%;
	}
	.product-category {
	    overflow: hidden;
	    height: 44px;
	    border-bottom: 1px solid #d9d9d9;    
	}
	.product-category .nav-info{
	    height: 44px;
	    line-height: 44px;
	    padding-bottom: 8px;
	    display: flex;
	    flex-wrap: nowrap;
	    white-space: nowrap;
	    overflow-x: auto;
	}

	.product-category .nav-info a {
	    display: block;
	    font-size: 1.4rem;
	    padding: 0 4%;
	}
	.product-category .nav-info a.active {
	    color:#fb7d34;
	}
</style>


