<template>
	<div>
		<div class="detail-banner">
		    <a href="javascript:history.back()"><i class="iconfont icon-back"></i></a>
		    <router-link to="/search"><i class="iconfont icon-search"></i></router-link>
		</div>
	  	<swiper-banner v-bind:bannerimgs="imgurls"></swiper-banner>
		<div class="product-info">
		    <div class="d-info">
		        <h2>{{ titleView.name }}</h2>
		        <p class="price"><strong>{{ titleView.price }}元</strong></p>
		        <p class="sub-title">
		            {{ titleView.product_desc }}
		        </p>
		        <div class="tag-box">
		            <span>白色</span>
		            <span>蓝色</span>
		        </div>
		    </div>
		    <div class="details-box">
		        <nav>
		            <a href="javascript:void(0);" v-bind:class="{active: activeIndex === 0}" v-on:click="tab(0)">概述</a>
		            <a href="javascript:void(0);" v-bind:class="{active: activeIndex === 1}" v-on:click="tab(1)">参数</a>              
		        </nav>
		        <section v-if="activeIndex === 0">
		            <img :src="item.plainView.img" v-if="index < showCount" v-for="(item,index) in descTabsView[0].tabContent">
		        </section>
		        <section v-if="activeIndex === 1">
		            <img :src="item.plainView.img" v-if="index < showCount" v-for="(item,index) in descTabsView[1].tabContent">
		        </section>
		    </div>
		</div>
		<recomment-items></recomment-items>

		 <footer class="bottom-banner">
		    <div>
		        <router-link to="/" class="back-btn">
		            <i class="iconfont icon-shouye"></i>
		        </router-link>
		        <a href="#" class="buy-btn">立即购买</a>
		        <router-link to="/cart" class="cart-btn">
		            <i class="iconfont icon-cart"></i>
		        </router-link>
		    </div>
		</footer>
		<go-top></go-top>
	</div>
</template>
<script>
	import SwiperBanner from './SwiperBanner'
	import RecommendItems from './RecommendItems'
	import GoTop from './GoTop'
	export default {
		name: 'product-details',
		components: {
    		'swiper-banner': SwiperBanner,
    		'recomment-items': RecommendItems,
    		'go-top': GoTop
		},
		data () {
			return {
				activeIndex: 0,
				showCount: 3,
				productId: this.$route.query.id,
				url: '/static/json/productView_'+this.$route.query.id+'.json',
				productData: {
					viewContent: {
						descTabsView: {
							descTabsView: [{tabContent:[]},{tabContent:[]}]
						},
						titleView: {
							titleView:{
								product_desc: ''
							}
						},
						galleryView: {
							galleryView:[]
						}
					}
				}
			}
		},
		computed: {
			imgurls () {
				var imgurls = [];
				this.productData.viewContent.galleryView.galleryView.forEach((item, index) => {
					imgurls.push({img_url: item});
				})
				return imgurls;
			},
			titleView () {
				return this.productData.viewContent.titleView.titleView;
			},
			descTabsView () {
				return this.productData.viewContent.descTabsView.descTabsView;
			}
		},
		methods: {
			tab (index) {
				this.activeIndex = index;
			},
			getProductData () {
				this.$http.get(this.url).then(function(response) {
					this.productData = response.body.data;
				}).catch(function(e) {
					this.$http.get('/static/json/productView.json').then(function(response) {
						this.productData = response.body.data;
					})
				})
			}
		},
		created: function() {
			this.getProductData()
		}
	}
</script>
<style scoped>
	.detail-banner {
	    position: absolute;
	    height: 44px;
	    left: 0;
	    top: 0;
	    width: 100%;
	    z-index: 10;
	}
	.detail-banner i {
	    position: absolute;
	    width: 20px;
	    height: 20px;
	    line-height: 20px;
	    text-align: center;
	    background-color: #000;
	    color: #fff;
	    top: 12px;
	    border-radius: 20px;
	    
	}
	.detail-banner .icon-back {
	    left: 20px;
	    font-size: 1.6rem;
	}
	.detail-banner .icon-search {
	    right: 20px;
	}
	.product-info .d-info{
	    padding: 4%;
	}
	.product-info h2 {
	    font-size: 1.6rem;
	    color: #333;
	}
	.product-info .price {
	    color:#ff5722;
	    font-size: 1.8rem;
	}
	.product-info .sub-title {
	    font-size: 1.2rem;
	    color: #999;
	    margin: 2% 0;
	}
	.product-info .tag-box {
	    display: flex;
	    flex-wrap: wrap;
	}
	.product-info .tag-box span{
	    padding: 4px 8px;
	    border: 1px solid #ff5722;
	    color: #666;
	    margin-right:4px;
	}

	.product-info .details-box {
	    border-top: 1px solid #d9d9d9;
	}
	.product-info .details-box nav {
	    display: flex;
	}
	.product-info .details-box a {
		font-size: 1.4rem;
	    position: relative;
	    flex: 1;
	    text-align: center;
	    height: 44px;
	    line-height: 44px;
	}
	.product-info .details-box a:before {
	    position: absolute;
	    left: 0;
	    height: 24px;
	    top: 10px;
	    content:"";
	    border-left: 1px solid #999;
	    color: #999;
	    font-size: 2.2rem;
	}
	.product-info .details-box .active {
	    color: #ff5722;
	}
	.product-info .details-box a:first-child:before {
	    display: none;
	}


	.bottom-banner {
		z-index: 1;
		height: 48px;
		margin-top: 4%;
	}
	.bottom-banner > div {
		position: fixed;
		width: 100%;
		bottom: 0;
		left: 0;
	    display: flex;
	    font-size: 1.6rem;
	    border-bottom: 1px solid #d9d9d9;
	    background: #f5f5f5;
		padding: 6px 0;
		border-top: 1px solid #d9d9d9;
	}
	.bottom-banner .buy-btn {
	    flex: 1;
	    text-align: center;
		background-color: #ff5722;
		color:#fff;
		line-height: 47px;
		border-radius: 4px;
	}

	.bottom-banner .back-btn, .bottom-banner .cart-btn  {
	    color: #999;
	    font-size: 1.8rem;
		padding: 4px 12px;
	}
	.bottom-banner .iconfont {
	    font-size: 2.6rem;
	}


</style>



