<template>
	<div>
		<search-topbar v-bind:title="title"></search-topbar>
		<no-login-tips></no-login-tips>
		<no-items></no-items>
		<recommend-items></recommend-items>
	</div>
</template>
<script>
	import SearchTopbar from './SearchTopbar'
	import NoLoginTips from './NoLoginTips'
	import NoItems from './NoItems'
	import RecommendItems from './RecommendItems'
	export default {
		name: 'cart',
		data () {
			return {
				title: '购物车'
			}
		},
		components: {
			'search-topbar': SearchTopbar,
			'no-login-tips': NoLoginTips,
			'no-items': NoItems,
			'recommend-items': RecommendItems
		}
	}
</script>



